.site-main{
	opacity: 1;
	transition: opacity .6s ease-in-out;
}
.site-search{
	position: absolute;
	margin-top: 64px;
	left: 0;
	right: 0;
	top: 0;
	opacity: 0;
	z-index: -1;
	transition: opacity .6s ease-in-out;
	padding: 32px 0 110px;
	@include mq($from: tablet ) {
		padding: 32px 0 0;
		margin-top: 71px;
	}
	&.active{
		opacity: 1;
		z-index: 110;
	}
	&_form{
		margin: 0 0 10px;
	}
	&_toolbar{
		// margin-top: 32px;
		overflow: hidden;
	}
	&_button-results{
		padding-top: 57px;
		@include mq($from: tablet ) {
			margin-bottom: 50px;
		}
		@include mq($from: desktop ) {
			margin-bottom: 182px;
		}
		&.fixed{
			@include mq($until: tablet ) {
				position: fixed;
				bottom: 0;
				left: 0;
				width: 100%;
				z-index: 25;
				padding: 15px 15px 25px;
				// padding: 15px 15px 90px;
			}
		}
	}
}
.btn-search{
	appearance: none;
	background-color: transparent;
	border: 0;
	color: get-color(red);
	cursor: pointer;
	display: inline-block;
	font-size: 23px;
	vertical-align: middle;
	position: relative;
	width: 30px;
	height: 30px;
	padding: 5px;
	margin-left: 10px;
	transition: all .6s ease-in-out;
	@include mq($from: tablet ) {
		width: 54px;
		height: 71px;
	}
	@include mq($from: desktop ) {
		margin-left: 40px;
		width: 68px;
		height: 71px;
    margin-top: -1px;
	}
	&--mobile{
		float: right;
		margin: 17px 15px;
		@include mq($from: tablet ) {
			display: none;
		}
	}
	&_icon{
		display: block;
		transition: opacity .3s;
		@include center();
	}
	.ic-search{
		width: 24px;
		height: 24px;
	}
	.ic-close,
	.ic-close-white{
		opacity: 0;
	}
}

body{
	&.search-active{
		.site-header{
			&_fixed{
				@include mq($until: tablet ) {
					background-color: get-color(bluedark);
					border-bottom: get-color(bluedark);
				}
			}
			.logo_text{
				@include mq($until: tablet ) {
					color: rgba(get-color(white),.8);
				}
			}
			.menu-trigger{
				display: none !important;
			}
			.goto{
				@include mq($until: tablet ) {
					display: block;
				}
			}
			.btn-search{
				background-color: get-color(bluedark);
				color: get-color(white);
			}
		}
		.site-main{
			overflow: hidden;
			opacity: 0;
			position: absolute;
			margin-top: 71px;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			z-index: -1;
		}
		
		.site-search{
			margin-top: 0;
			opacity: 1;
			z-index: 111;
			position: relative;
		}
		#search{
			margin-top: 0;
		}
		#searchHero{
			
			// margin-top: -147px;
			@include mq($from: tablet ) {
				// margin-top: -77px;
			}
		}
		.btn-search{
			.ic-close,
			.ic-close-white{
				// display: block;
				opacity: 1;
			}
			.ic-search{
				// display: none;
				opacity: 0;
			}
		}
	}
}

.search{
	&_section{
		background-color: rgba(get-color(white),.05);
		// margin: 0 0 25px;
		@include mq($from: tablet ) {
			// margin: 0 0 25px;
		}
    @include mq($from: desktop ) {
			margin: 0 0 100px;
			height: 385px;
			overflow: hidden;
		}
		@include mq($from: wide ) {
			height: 345px;
		}
		.new{
			margin-bottom: 0 !important;
		}
	}
}
.result{
	padding: 10px 0;
	&_body{
		padding-left: 25px;
		p{
			font-size: 14px;
			margin: 0;
			small{
				color: rgba(get-color(dark), .4);
			}
		}
	}
}