	}
	&_menu{
		.link{
			padding-top: 8px;
			padding-bottom: 8px;
		}
		// Do counter
	}
	&_slider{
		margin-left: -15px;
		margin-right: -15px;
	}
}

.article{
	&_section{
		padding: 20px 0;
		&:not(:first-child){
			border-top: 1px solid get-color(lightgray);
		}
		@include mq($until: tablet ) {
