.ctag,
.filter{
	// background-color: get-color(red);
	background-color: rgba(get-color(dark), .1);
	border: 2px solid transparent;	
	// border-radius: 3px;
  color: rgba(get-color(dark), .6);
  font-size: ms(-1);
  line-height: ms(0);
  display: inline-block;
  margin: ms(-1) 0;
  padding: 13px 18px;
  margin: 0 8px ms(-1) 0;
  height: 42px;
  position: relative;
  text-align: center;
	text-decoration: none;
	white-space: nowrap;
	&:hover,
	&.hover{
		background-color: get-color(red);
		// color: get-color(white); 
	}
	&--clarity{
		background-color: rgba(get-color(white), .1);
		color: rgba(get-color(white), .8);
	}
	
}

.ctag{
	&.active{
		background-color: transparent;
		border: 2px solid rgba(get-color(light), .3);
		display: block !important;
		&:after{
			// background: transparent url($iconsPath+ 'delete.svg') center center no-repeat;
			// content:"";
			@include getIcon("\6f");
			color: get-color(gray);
			font-size: 17px;
			display: inline-block;
			vertical-align: middle;
			width: 17px;
			height: 17px;
			margin-top: -2px;
			z-index: 10;
			margin-left: 15px;
		}
		&:hover,
		&.hover{
			color: get-color(white);
		}
	}
	&--light{
		&.active{
			border: 2px solid get-color(gray);
			border-radius: 3px;
			&:hover,
			&.hover{
				color: get-color(dark);
			}
		}
	}
}
.filter{
  color: get-color(dark);
	&:hover{
		color: get-color(dark);
		background-color: rgba(get-color(dark),.25);
	}
	&.active{
		background-color: get-color(red);
		// color: get-color(white); 
	}
}


.tags{
	padding: 0 15px;
	// width: 100%;
	margin-bottom: 15px;
	overflow-x: auto;
	overflow-y: hidden;
	-webkit-overflow-scrolling: touch;
	-ms-overflow-style: -ms-autohiding-scrollbar;
	@include mq($from: tablet ) {
		// padding: 20px 0 0;
		padding: 0;
	}
	&_content{
		display: table;
		padding-bottom: 8px;
		@include mq($from: tablet ) {
			display: block;
		}
	}
	&_item{
		display: table-cell;
		vertical-align: middle;
		table-layout: fixed;
		@include mq($from: tablet ) {
			display: inline-block;
		}
	}
}